import React from "react"
import { Link } from "react-router-dom";

export default class EmbedViewerOnWebflow extends React.Component {
    render() {
        return (
            <div className='help-page'>
                <h1>Integrate the viewer with your Webflow site</h1>
                <h2>Add the viewer to a product page</h2>
                <h4>1. link a Webflow product to an Ezyvue product</h4>
                <ol>
                    <li>Copy the Webflow product id from heading item ID on the product page</li>
                    <li>Paste it into the External Product ID textbox in the Metadata section of the Manage Project page.</li>
                    <li>Click Save Changes.</li>
                </ol>
                <h4>2. Add an Embed element to the products template page</h4>
                <ol>
                    <li>Select Pages and select Products template under Ecommerce pages.</li>
                    <li>Select Add and under Components select Embed to add the element to the page.</li>
                </ol>
                <h4>3. Add code to the Embed element</h4>
                <ol>
                    <li>Select the Embed element on the canvas and double-click to open the code editor.</li>
                    <li>Copy and paste the code below into the editor:
                    <pre>{`
<div id="ezyvue-viewer-container" style="width:400px;height:400px;display:none">
    <iframe id="ezyvue-viewer" width="100%" height="100%" style="border:none; "frameborder="0" xr-spatial-tracking="true" autoplay allowfullscreen mozallowfullscreen="true" webkitallowfullscreen="true" allow="fullscreen"></iframe>
</div>
<script>
    var urlParams = new URLSearchParams(document.location.search);
    var productId = urlParams.get('itemId');
    if(productId) {
        var xmlhttp = new XMLHttpRequest();
        var url = 'https://www.ezyvue.com/api/product?externalProductID=' + productId;

        xmlhttp.onreadystatechange = function() {
            if (this.readyState == 4 && this.status == 200) {
                    var evProduct = JSON.parse(this.responseText);if(evProduct) {
                    var iframe = document.getElementById("ezyvue-viewer");
                    var options = '&hidemenu';
                    
                    iframe.src = 'https://www.ezyvue.com/viewer?' + 'productId=' + evProduct._id + options;
                    
                    var el = document.getElementById("ezyvue-viewer-container");
                    if(el)
                        el.style.display = 'block';
                }
            }
        };
        
        xmlhttp.open("GET", url, true);
        xmlhttp.send();
    }
</script>
                    `}</pre>
                    </li>
                    The 3d viewer should appear on the page.
                </ol>
                <h4>4. Set the viewer options</h4>
                Note: product-specific settings can be set in the settings tab of Ezyvue Studio.
                <ol>
                    <li>Navigate to the <Link to='/embed'>integrate viewer page</Link> and choose the required settings.</li>
                    <li>Replace the &amp;hidemenu option in the code above with the options from the HTML code textbox. (Each option begins with the character &amp;).</li>
                </ol>
                <h2>Add the viewer for a specific product</h2>
                <p>
                    You can embed the viewer on any template page by addding an iframe to an Embed element.
                    Follow this <Link to='/help/embedViewer'>guide</Link>
                </p>           
            </div>
        );
    }
}

